import React from "react";
import { useMemo } from "react";
import { getClassName } from "tools/getClassName";
import { useLocalizedText } from "hooks/useLocalizedText";
import { CommonTextProps } from "components/Text/Text";
import { FONT_COLORS, TFontColors } from "../../colors.const";

import './Title.scss';

type TitleProps = CommonTextProps & {
    as?: 'h1' | 'h2' | 'h3' | 'span',
    color?: TFontColors,
    className?: string,
    subtitle?: boolean,
}

const cn = getClassName('title');

export const Title = ({
  as,
  color = 'black',
  className,
  subtitle,
  ...textProps
}: TitleProps) => {
  const Component = as ?? 'span';
  const style = useMemo(() => ({ color: FONT_COLORS[color] }), [color]);
  const text = useLocalizedText(textProps);

  return <Component style={style} className={cn({ subtitle }, className)}>{text}</Component>;
};
