// Components to provide the necessary contexts to page 
import React from 'react';
import { IntlContextProvider } from "gatsby-plugin-intl";
import { CookieConsent } from 'components/CookieConsent/CookieConsent';

import { PageWrapperProps } from "./Page.props";

export const Page = ({ children, pageContext }: PageWrapperProps) => {
  return (
    <IntlContextProvider value={pageContext.intl}>
      {children}
      <CookieConsent />
    </IntlContextProvider>
  );
};
