import React from 'react';
import { useIntl } from 'gatsby-plugin-intl';
import { Text } from 'components/Text/Text';
import { Title } from 'components/Title/Title';
import { CookieConsent as ReactCookieConsent } from'react-cookie-consent';
import { Button } from 'components/Button/Button';

import { getClassName } from 'tools/getClassName';

const cn = getClassName('cookie-consent');

import './CookieConsent.scss';

export const CookieConsent = () => {
  const intl = useIntl();

  return (
    <ReactCookieConsent
      disableStyles
      containerClasses={cn()}
      buttonWrapperClasses={cn('buttons')}  
      enableDeclineButton
      buttonClasses={cn('button')}
      declineButtonClasses={cn('button')}
      buttonText='Accept'
      declineButtonText='Reject'
      ButtonComponent={CookieButton}
    >
      <Title color='white'>cookie-consent-title</Title>
      <Text color='white'>cookie-consent-statement</Text>
      <Text color='white'>cookie-consent-action-point</Text>
    </ReactCookieConsent>);
};

const CookieButton = ({ children, ...props }: React.HTMLAttributes<HTMLButtonElement>) => {
  return (<Button {...props} color='orange' >
    <Text color='white'>{children as string}</Text>
  </Button>);
};
